<div><p class="text-start title-style cursor" @click="setGroupStatus()"><i class="mr-1" :class="groupedStatusStyle"> </i>角色-zone {{ index+1 }}</p><div class="d-flex roles-style cursor" v-for="(pair,index) in data" @click="setSingleStatus(pair,index)"><div class="mr-1"><i class="bi" :class="setSingleCheckColor(pair.checked,pair.required)"> </i></div><div class="d-flex justify-content-between flex-grow-1"><p class="pole-style w-50 text-start" v-for="person in pair?.roles"><i class="bi bi-person-badge icon-color" :style="SetTeamColor(person.color)"> </i>{{ person.label }}</p></div></div></div>