<div class="wrap position-relative"><div class="banner-wrap"><div class="mx-1280"><div class="banner-img-wrap"><img src="@/assets/pic/logo-red.png"></div></div></div><div class="setting-wrap mx-1280 flex-grow-1"><h1 class="title-style text-start mb-6">創建遊戲</h1><div class="d-flex align-items-center mb-4"><div class="d-flex align-items-center"><p class="text-nowrap fw-bold">卡片牌局：</p><BFormSelect v-model="selectedModeId" :options="roleOptions"></BFormSelect></div><div class="count-wrap"><p class="d-inline-block fw-bold">人數：<span class="fw-normal">{{ countChecked }}</span><span class="fw-bold fs-red ml-2" v-if="countChecked&gt;20">(人數已達上限20人)</span></p></div></div><div class="d-flex flex-wrap group-card-wrap flex-grow-1"><GroupCardPanel class="group-card-panel" v-for="(roles,index) in filteredRoles" :data="roles" :index="index" @setGroupStatus="setGroupStatus" @setSingleStatus="setSingleStatus"></GroupCardPanel></div><div class="d-flex justify-content-center"><div class="button-style cursor fw-bold" @click="submitSetting">Game Start</div></div></div><FooterCopyright class="position-absolute bottom-0 start-0"></FooterCopyright></div>