<div class="position-relative configuration-wrap"><CountdownTimer class="position-absolute timer-wrap" :gameRound="gameRound " @addGameRound="addGameRound"></CountdownTimer><div class="position-absolute ver-wrap d-flex flex-column"><div class="w-100 d-flex justify-content-center bi-number-style-wrap pb-50 h-50 align-items-center"><div><div class="title-style">{{ gameMode.label }}局</div><div class="subtitle-style rb-fz">{{ gameMode.text.toUpperCase() }}</div><div class="subtitle-style rb-fz">ROUND</div><div class="number-style-wrap icon-text-style"><i class="bi bi-number-style cursor" v-for="i in 5" :class="i==gameRound+1?`bi-${i}-circle-fill`:`bi-${i}-circle`" @click="changeGameRound(i)"></i></div></div></div><div class="w-100 pt-50 h-50 d-flex justify-content-center align-items-center"><div class="px-6"><p class="text-start icon-text-style">討論時間結束後<span class="d-block">請室長在1分鐘內</span><span class="d-block">選出 {{quantity}} 位人質</span></p><div class="hostage-icon-container"><i class="bi bi-person-walking" v-for="i in quantity"></i><i class="bi bi-door-open door"></i></div></div></div></div><div class="position-absolute ho-wrap d-flex"><div class="w-50 pr-25" :style="{'background-color':leftColor}"><div class="mx-auto pic-wrap"><img class="d-block" src="@/assets/pic/logo-red.png"></div></div><div class="w-50 pl-25" :style="{'background-color':rightColor}"><p class="player-style rb-fz">{{ playerCount }}<span class="ml-2">Players</span></p></div></div></div>